<script>
export default { 
  name: 'App',
  data() {
    return{
      city: "",
      citycode: 0,
      show: false,
      cities: {},
      results:{},
      oqat:{},
    }
  },
  mounted(){
    this.getCities();
  },
  methods:{
    async getCities(){
      let q = await fetch("https://prayer.aviny.com/api/city").then(response => response.text());
      this.cities = JSON.parse(q);
      //console.log(typeof(this.cities));
      delete this.results;
      this.results = this.cities;
    },
    async choose(code){
      //alert(code);
      this.citycode = code;
      let p = await fetch('https://prayer.aviny.com/api/prayertimes/'+code).then(
        response => response.text());
      this.oqat = JSON.parse(p);
      this.show = true;
      console.log(this.oqat);
    },
    back(){
      this.show = false;
    },
    clearInput(){
      this.city = "";
      this.onChange();
    },
    onChange(){
      if(this.city == ""){
        delete this.results;
        this.results = this.cities;
      }else{
        delete this.results;
        let res = {};
        let len=Object.keys(this.cities).length
        let re = RegExp('^'+this.city);
        //let p = 0;
        for(let i=0; i < len;i++){
          if(re.test(this.cities[i].Name) == true){
            res[i] = {
              Name:this.cities[i].Name,
              Code:this.cities[i].Code}
            //  p++;
          }
        }
        this.results = res;
        console.log("done");
      }
    }

  },
  components: {
    
  }
}
</script>

